.release {
  font-weight: 500;
  width: 67.70833vw;
  margin: 1.30208vw auto 0;
}
.release-titleWrap {
  height: 3.125vw;
  line-height: 3.125vw;
}
.release-title {
  width: 41.66667vw;
}
.release-submit {
  margin-top: 0.70313vw;
  float: right;
}
.release-type {
  display: flex;
  margin-bottom: 1.04167vw;
}
.release-Tag {
  cursor: pointer;
  text-align: center;
}
.release-TagItem {
  color: #fff;
  background: #333;
}
.release .BraftEditor {
  border: 1px solid #ccc;
  max-height: calc(100% - 20%);
  overflow-y: auto;
}
.release pre {
  position: relative;
  margin-bottom: 1.25vw;
  border-radius: 0.15625vw;
  border: 1px solid #C3CCD0;
  background: #FFF;
  overflow: hidden;
}
.release code {
  display: block;
  padding: 0.625vw 1.25vw;
  overflow-y: auto;
  font-weight: 300;
  font-family: Menlo, monospace;
  font-size: 0.8em;
}
.release code.has-numbering {
  margin-left: 1.09375vw;
}
.release .pre-numbering {
  position: absolute;
  top: 0;
  left: 0;
  width: 1.04167vw;
  padding: 0.625vw 0.10417vw 0.625vw 0;
  border-right: 1px solid #C3CCD0;
  border-radius: 0.15625vw 0 0 0.15625vw;
  background-color: #EEE;
  text-align: right;
  font-family: Menlo, monospace;
  font-size: 0.8em;
  color: #AAA;
}
.hljs {
  border: 0;
  font-family: "Consulas", "Courier New", Courier, mono, serif;
  font-size: 0.625vw;
  background: #eee !important;
  display: block;
  padding: 1px;
  margin: 0;
  width: 100%;
  font-weight: 200;
  color: #333;
  white-space: pre-wrap;
}
.hljs ul {
  list-style: decimal;
  background-color: #fff;
  margin: 0px 0px 0 2.08333vw !important;
  padding: 0px;
}
.hljs ul li {
  list-style: decimal-leading-zero;
  border-left: 1px solid #ddd !important;
  background: #fff;
  padding: 0.26042vw!important;
  margin: 0 !important;
  line-height: 0.72917vw;
  word-break: break-all;
  word-wrap: break-word;
}
.hljs ul li:nth-of-type(even) {
  background-color: #fcfcfc;
  color: inherit;
}
pre {
  display: block;
  padding: 0.52083vw;
  margin: 0 0 0.52083vw;
  font-size: 0.72917vw;
  line-height: 1.42857143;
  color: #abb2bf;
  background: #282c34;
  word-break: break-all;
  word-wrap: break-word;
  overflow: auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 1em;
  /* margin-bottom: 1em; */
}
strong {
  font-weight: bold;
}
p > code:not([class]) {
  padding: 0.10417vw 0.20833vw;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 0.20833vw;
}
p img {
  /* 图片居中 */
  margin: 0 auto;
  display: flex;
}
#content {
  font-family: "Microsoft YaHei", 'sans-serif';
  font-size: 0.83333vw;
  line-height: 1.5625vw;
}
#content .desc ul,
#content .desc ol {
  color: #333333;
  margin: 1.5em 0 0 1.30208vw;
}
#content .desc h1,
#content .desc h2 {
  border-bottom: 1px solid #eee;
  padding-bottom: 0.52083vw;
}
#content .desc a {
  color: #009a61;
}
